.app-bar {
  position: relative;
}

.root {
  position: relative;
  background: #525659;
  min-height: 100%;
  max-height: 100%;
  display: flex;
  flex-direction: column;

  .controls {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px;
    background-color: #323639;
    // position: -webkit-sticky;
    // position: sticky;
    // z-index: 999;
    top: 0;

    .control {
      span {
        color: white;
        font-size: 17px;
      }

      &.disabled {
        span {
          color: #636363;
        }
      }
    }
  }

  .viewport {
    overflow: auto;
    flex: 1;
    display: flex;
    max-width: 100%;
    .pdf {
      margin: auto;
    }
  }

  .page-number {
    color: #dddddd;
    font-weight: 600;
  }
  .file-name {
    color: #dddddd;
    font-weight: 600;
    margin-right: 50px;
  }
}
